
import ShopBtn from '@/components/shopBtn'
import shopList from '@/components/shopList'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

import {getOrderRefundStatus, cancelOrderRefund, submitOrderRefundRoute, getCoustemURL} from '@/api/user'

export default {
name: 'uc-refundDetail',
data () {
return {
defaultValue: '顺丰',
refundInfo: {},
routenum: '', // 退款 快递单号
listData: [
{
img: 'static/images/order_a.png',
titL: 'LA310空净机器人',
titR: '￥2980',
subtitL: '流金沙',
subtitR: 'X1'
}
],
show: false,


list: [{key: '顺丰', value: '顺丰'}, {key: '韵达', value: '韵达'}, {key: '申通', value: '申通'}, {key: '圆通', value: '圆通'}, {key: '中通', value: '中通'}, {key: '百世', value: '百世'}, {key: '其他', value: '其他'}]
}
},
filters: {
format: function (val) {
const str1 = val.substring(0, 3)
const str2 = val.substring(7, 11)
const str3 = str1 + '****' + str2
console.log(str1, str2)
return str3
},
statusFilter (val) {
let res = ''
switch (val) {
case 'waittingpay':
res = '待付款'
break
case 'payed':
res = '已付款'
break
case 'payedone':
res = '预付一阶段'
break
case 'waittingsecpay':
res = '预付二阶段'
break
case 'sent':
res = '已发货'
break
case 'recieve':
res = '已签收'
break
case 'finish':
res = '已完成'
break
case 'refunding':
res = '退款中'
break
case 'agreerefund':
res = '同意退款'
break
case 'reject':
res = '拒绝退款'
break
case 'waittingconfirm':
res = '退款待确认'
break
case 'refunded':
res = '退款完成'
break
case 'overtime':
res = '超时关闭'
break
case 'cancel':
res = '取消关闭'
break
default:
res = '未知'
break
}
return res
}
},
created () {
const orderid = this.$route.query.id
console.log(orderid)
const postData = {
orderid: orderid
}

getOrderRefundStatus(postData)
.then(res => {
if (res.data.errorcode === 0) {

console.log(res)
this.refundInfo = res.data.data

} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},
components: {
ShopBtn,
shopList,
Cell,
Group,
Popup,
XInput,
Selector
},
methods: {
gotoURL () {
const _this = this
const postData = {}
getCoustemURL(postData)
.then(res => {
if (res.data.errorcode === 0) {

window.location.href = res.data.data.url
} else {
_this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
_this.$vux.loading.hide()
console.log(err)
_this.$vux.toast.text('网络异常！', 'middle')
})
},
handleCancel: function (type) {

let contenttxt = '确定要取消申请吗？'
if (type == 2) contenttxt = '确定要重新申请吗？'

const _this = this
this.$vux.confirm.show({
title: '提示',
content: contenttxt,

onCancel () {
},
onConfirm () {
console.log(12)
_this.$vux.loading.show()
const postData = {
orderid: _this.refundInfo.orderid
}
cancelOrderRefund(postData)
.then(res => {
_this.$vux.loading.hide()
if (res.data.errorcode === 0) {

_this.$vux.toast.text('操作成功', 'middle')
if (type == 1) {
_this.$router.replace({
name: 'myOrder'
})
} else {
_this.$router.replace({
name: 'uc-submitRefunds', query: { id: _this.refundInfo.orderid}
})
}
} else {
_this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
_this.$vux.loading.hide()
console.log(err)
_this.$vux.toast.text('网络异常！', 'middle')
})
}
})
},
handleSave () {
const _this = this
if (_this.routenum === '') {
_this.$vux.toast.text('请填写物流信息', 'middle')
return
}
if (_this.defaultValue != '')
{
_this.routenum = _this.defaultValue + ":" + _this.routenum;
}

this.$vux.confirm.show({
title: '提示',
content: '确定信息无误？',

onCancel () {
},
onConfirm () {
console.log()
_this.$vux.loading.show('loading')
const postData = {
orderid: _this.refundInfo.orderid,
routenum: _this.routenum
}
submitOrderRefundRoute(postData)
.then(res => {
_this.$vux.loading.hide()
if (res.data.errorcode === 0) {

_this.$vux.toast.text('操作成功', 'middle')
_this.$router.replace({
name: 'myOrder'
})
} else {
_this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
_this.$vux.loading.hide()
console.log(err)
_this.$vux.toast.text('网络异常！', 'middle')
})
}
})
}
}
}



